.maincontainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 3px solid #918ca7;
  gap: 0.5rem;
  padding: 0.5rem;
  margin: 1rem;
  background-color: #171721;
}

.columnBlock{
  display: flex;
  flex-flow: column;
  flex: 1 1 auto;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  border: 3px solid #918ca7;
}

.lines{
  display: flex;
  flex: 1 1 auto;
  align-self: stretch;
  gap: 1rem;
  padding: 0 0.3rem;
  margin: 0 0.3rem;
}

.linesText{
  display: flex;
  flex: 1 1 auto;
  align-self: stretch;
  align-items: center;
  gap: 1rem;
  padding: 0 0.3rem;
  margin: 0 0.3rem;
}

.linesTextCH {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  align-self: stretch;
  align-items: center;
  gap: 1rem;
  padding: 0 0.3rem;
  margin: 0 0.3rem;
  border: 1px solid #918ca7;
}

.columns{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  flex: 1 1 auto;
  align-self: stretch;
  gap: 0.3rem;
  margin: 0.3rem 0;
  padding: 0.3rem 0;
}

.columnsText{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  flex: 1 1 auto;
  align-self: stretch;
  gap: 0.3rem;
  margin: 0.3rem 0.3rem;
  padding: 0.3rem 0.3rem;
  border: 1px solid #918ca7;
}

.stat{
  cursor: pointer;
}

.mainStat{
  cursor: pointer;
  color: yellow;
}
